import React from 'react'
import './Header.scss'
import { Button } from 'antd'

const Header = props => {
  return (
    <div className="header-box">
      <div className="user-content">
        {props.data?.patientName ? (
          <span>
            {props.data.patientName} {props.data.medicalCaseCode} 
            {props.data.patientSex_dictText} {props.data.age} {props.data.createTime}
          </span>
        ) : null}
      </div>
      {props.pageType === 'review' ? (
        <div className="export">
          <Button type="primary" onClick={props.handleShowModal}>
            
          </Button>
        </div>
      ) : null}
    </div>
  )
}

export default Header
